body {
    background-color: #080b14;
}

h1 {
    text-align: center;
    font-size: 50px;
    color: #bcc4db;
    margin: 0px;
}

p {
    text-align: center;
    font-size: 25px;
    color: #bcc4db;
}

.smallMargins {
    margin-top: 0px;
    margin-bottom: 0px;
}

hr {
    color: #bcc4db;
}

button {
    width: 40px;
    height: 40px;
    border-radius: 0;
    border: 2px solid #bcc4db;
    font-size: 30px;
    color: #bcc4db;
}

.toggleCompact {
    position: relative;
    left: 102px;
    width: 85px;
    background-color: #080b14;
    transition: filter .15s ease;
        transition-property: filter;
        transition-duration: 0.15s;
        transition-timing-function: ease;
}

.toggleCompact:hover {
    filter: brightness(2);
}

.gray {
    background-color: #080b14;
}

.yellow {
    background-color: #ffd651;
    color: #080b14;
    border: 0px
}

.green {
    background-color: #65fda0;
    color: #080b14;
    border: 0px
}

.solvedGray {
    background-color: #080b14;
    opacity: 0.25;
}

.solvedYellow {
    background-color: #ffd651;
    color: #080b14;
    border: 0px;
    opacity: 0.25;
}

.solvedGreen {
    background-color: #65fda0;
    color: #080b14;
    border: 0px;
    opacity: 0.25;
}

.row {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(auto-fit, 45px);
    margin-top: 5px;
    margin-bottom: 5px;
}

.board {
    margin-bottom: 25px;
    margin-left: 5px;
}

.boards {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(6, 300px);
}

.guessStats {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
}

.bg {
    justify-content: center;
    margin-top: 10px;
    height: auto;
    width: 200px;
    border: 2px solid #bcc4db;
    border-radius: 10px;
}

a {
    text-align: center;
    font-size: 25px;
    color: #bcc4db;
}